@import './default';

.container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 12.5rem 0;
    position: relative;
    max-width: 1160px;

    @media (min-width: 800px) {
        justify-content: space-between;
        flex-direction: row;
    }
}

.container__nav {
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;

    ul {
        display: flex;
        padding: 1.5rem 0;
    }

    li {
        margin-left: 2rem;
    }
}

.container--hero {
    max-width: 1000px;
    min-height: 100vh;
    padding-top: 0px;
}

.container--form {
    max-width: 800px; 
}

.container__content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;

    p {
        max-width: 320px;
    }
}

.container__img {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.bg_img {
    position: absolute;
    z-index: -1;

    &.bg_hero-right {
        right: 0;
        top: 0;
        transform: translate(80%, -10%);
    }

    &.bg_trace-01 {
        left: 0;
        transform: translate(-270px, 0px);
    }

    &.bg_trace-02 {
        top: 0;
        left: 0;
        transform: translate(-450px, -350px);
    }

    &.bg_trace-03 {
        left: 0;
        transform: translate(-350px, 50px);
    }

    &.bg_trace-04 {
        left: 0;
        transform: translateX(-200px);
    }
}

.contact__row {
    display: flex;
    justify-content: space-between;
}

.input {
    width: 100%;
    margin-bottom: 3.75rem;

    label {
        display: block;
        margin-bottom: 20px;
    }

    textarea,
    input {
        border: none;
        border-bottom: #707070 2px solid;
        width: 100%;
        padding-bottom: 30px;
    }

    + .input {
        margin-left: 150px;
    }
}